<template>
  <div class="Post">
    <h1>Posts</h1>
    <label for="create">Post</label>
    <input type="text" class="create" v-model="text">
    <button v-on:click="createPost">Create</button>
<p class="error" v-if="error"> {{error}}</p>
    <ul>
      <li v-for="post in posts" v-bind:item="post" v-bind:key="post._id" v-on:dblclick="deletePost(post._id)"> 
        <span>Post Created: {{`${post.createdAt.getDate()}/${post.createdAt.getMonth()}/${post.createdAt.getFullYear()}`}}</span>
       <p> {{post.text}} </p> 
        <hr>
        </li>
        
    </ul>
  </div>
   
</template>

<script>
import PostManagement from '../PostManagement';

export default {
  name: 'Post',
  data() {
    return {
      posts: {},
      error: '',
      text: ''
    };
  },
  async created() {
    try {
      this.posts = await PostManagement.getPosts();
    } catch (err) {
      this.error = err.message;
    }
  },
  methods: {
    async createPost() {
      await PostManagement.insertPost(this.text);
      this.posts = await PostManagement.getPosts();
      this.text = '';
    },
    async deletePost(id) {
      await PostManagement.deletePost(id);
      this.posts = await PostManagement.getPosts();
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.Post {
  margin: 0 auto;
  padding: 0;
  max-width: 980px;
}
.Post ul {
  list-style-type: none;
  padding: 0;
}
.Post ul li p {
  color: #35495e;
}
span {
  color: #41b883;
}
h1 {
  border-bottom: 1px solid black;
  border-top: 1px solid black;

  margin: 0 auto;
}
</style>
